import { format, formatDuration } from "date-fns";
import { useDispatch } from "react-redux";
import { setID } from "../Redux/task";
import { Checkbox } from "./checkbox";
import { ITask } from "./interfaces";
import { Priority } from "./priority";

interface props {
  obj: ITask[];
  task: ITask;
  id: string;
  finishTask(taskNameToDelete: string, id: string): void;
}
var conditionalClass: string;

export function TaskBody({ task, obj, finishTask, id }: props) {
  const dispatch = useDispatch();

  const handleDeets = () => {
    dispatch(setID(id));
    console.log("TASK obj ", task);
  };

  const Due = () => {
    if (task.deadline !== "" && task.deadline !== undefined) {
      var daysLeft: number =
        parseInt(format(new Date(task.deadline), "dd")) -
        parseInt(format(new Date(), "dd"));
      let M = format(new Date(task.deadline), "MM");
      let D = format(new Date(task.deadline), "dd");
      let Y = format(new Date(task.deadline), "yyyy");

      var duration = formatDuration(
        {
          years: parseInt(Y),
          months: parseInt(M),
          days: parseInt(D),
        },
        { format: ["months", "days"] }
      );
      console.log(duration);

      var day: string = "";
      var display: Boolean | undefined = undefined;
      if (daysLeft > 1) {
        day = " days";
        display = true;
        conditionalClass = "";
      }
      if (daysLeft === 0) {
        day = " today";
        display = false;
        conditionalClass = "";
      }
      if (daysLeft === 1) {
        day = " tomorrow";
        display = false;
        conditionalClass = "";
      }
      if (daysLeft < 0) {
        day = " overdue";
        display = false;
        conditionalClass = "text-danger";
      }

      return (
        <div className="littleGap">
          <p>{display === true ? daysLeft : ""}</p>
          <p className={conditionalClass}>{day}</p>
          {/* <p>{sub}</p> */}
        </div>
      );
    } else {
      return <span></span>;
    }
  };

  return (
    <>
      <div className="mt-2 taskRow" onClick={handleDeets}>
        <div className="w-100 mx-2 taskRow">
          <div className="d-flex flex-row flex-nowrap">
            <div
              className="checkBtn me-2"
              onClick={() => {
                finishTask(task.task, task.id);
              }}
            >
              <Checkbox col="#3e90cc" />
            </div>

            <p>{task.task}</p>
          </div>
          <div className="d-flex flex-row flex-nowrap align-items-center littleGap">
            <Due />
            <Priority type={task.priority} />
          </div>
        </div>
      </div>
    </>
  );
}
