import { useState } from "react";
import { GoogleLogin } from "react-google-login";
var crypto = require("crypto-js");
var reloadTO = "http://localhost:3000/#/";

function Account() {
  const [nick, setnick] = useState("");
  const [mail, setmail] = useState("");
  const [pass, setpass] = useState("");
  const [message, setmessage] = useState("");
  const [form, setform] = useState("signup");

  function encrypt() {
    // Encrypt
    var ciphertext = crypto.AES.encrypt(
      JSON.stringify([nick, mail, pass]),
      "secret key 123"
    ).toString();

    // Decrypt
    var bytes = crypto.AES.decrypt(ciphertext, "secret key 123");
    var decryptedData = JSON.parse(bytes.toString(crypto.enc.Utf8));
    return [ciphertext, decryptedData];
  }
  function registration(e: any, login?: boolean) {
    e.preventDefault();

    if (nick === "" || mail === "" || pass === "") {
      setmessage("no empty values");
    } else if (pass.length <= 5) {
      setmessage("password length must be greater than 5");
    } else {
      setmessage("");
      console.log(encrypt());
      localStorage.setItem(
        "profileP",
        "https://www.pngall.com/wp-content/uploads/2016/07/Success-PNG-Image.png"
      );
      localStorage.setItem("logged", "true");
    }
  }

  function Logout() {
    const deleteuser = () => {
      localStorage.setItem("logged", "false");
      localStorage.removeItem("profileP");
      setInterval(() => {
        window.location.href = reloadTO;
      }, 3000);
    };

    return <button onClick={deleteuser}>logout</button>;
  }

  const responseGoogle = (response: any) => {
    console.log(response.profileObj);
    localStorage.setItem("profileP", response.profileObj.imageUrl);
    localStorage.setItem("logged", "true");

    // setInterval(() => {
    //   window.location.href = reloadTO;
    // }, 3000);
  };
  if (localStorage.getItem("logged") === "true") {
    return (
      <div className="forms">
        {" "}
        <p>
          you're logged in <Logout />
        </p>
      </div>
    );
  } else if (form === "login") {
    return (
      <div className="forms">
        <p>log in</p>
        <form onSubmit={(e) => registration(e, true)}>
          <input
            type="email"
            placeholder="email"
            onChange={(e) => setmail(e.target.value)}
          />
          <input
            type="password"
            placeholder="password"
            onChange={(e) => setpass(e.target.value)}
          />
          <input className="btnttActive" type="submit" value="join" />
        </form>
        <p>
          {" "}
          <button className="btnttActive" onClick={() => setform("register")}>
            register
          </button>{" "}
          if you do not have an account
        </p>
      </div>
    );
  } else {
    return (
      <div className="forms">
        <p>Register</p>
        <form onSubmit={(e) => registration(e)}>
          <input
            type="text"
            placeholder="name"
            onChange={(e) => setnick(e.target.value)}
          />
          <input
            type="email"
            placeholder="email"
            onChange={(e) => setmail(e.target.value)}
          />
          <input
            type="password"
            placeholder="password"
            onChange={(e) => setpass(e.target.value)}
          />
          <input className="btnttActive" type="submit" value="join" />
        </form>
        <p className="text-danger">{message}</p>
        <br />
        <p>
          {" "}
          <button className="btnttActive" onClick={() => setform("login")}>
            log in
          </button>{" "}
          if you have an account
        </p>
        <div className="mt-4">
          <GoogleLogin
            clientId="567532719819-dmclf5l4u9nsol7odp1udsaslgst2kcs.apps.googleusercontent.com"
            buttonText="Login"
            onSuccess={responseGoogle}
            onFailure={responseGoogle}
            cookiePolicy={"single_host_origin"}
          />
        </div>
      </div>
    );
  }
}

export default Account;
